body {
	font-family: 'Nunito', sans-serif;
	margin: 0;
	padding: 0;
}

a {
	color: #f2f2f2;
	font-weight: bold;
}

#app {
	display: -ms-flexbox;
	display: flex;
	height: 100vh;
	color: #42413f;
}

#sub-list {
	width: 250px;
	background: #464646;
	color: #e6e5e6;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

#sub-list a {
	cursor: pointer;
	text-align: center;
	border: 1px solid #3a3a3a;
	width: 100%;
	background-color: #4c4c4c;
}

#feed-list {
	padding: 30px 0;
	-ms-flex: 2 1;
	    flex: 2 1;
	overflow-y: auto;
	overflow-x: hidden;
	color: #e6e5e6;
	background: #464646;
	border-left: 1px solid #3a3a3a;
	border-right: 1px solid #3a3a3a;
}

#feed-list::-webkit-scrollbar {
	width: 7px;
	background: #464646;
}

#feed-list::-webkit-scrollbar-thumb {
	background: #696969;
	border-radius: 7px;
}

#feed-list::-webkit-scrollbar-thumb:hover {
	background: #929292;
	border-radius: 7px;
}

#feed-list::-webkit-scrollbar:hover {
	background: #535353;
}

#story-container {
	-ms-flex: 3 1;
	    flex: 3 1;
	overflow: auto;
	color: #e6e5e6;
	background: #4c4c4c;
	padding: 40px 80px;
}

#story-container .story-title {
	-webkit-transition: background-color 500ms;
	-o-transition: background-color 500ms;
	transition: background-color 500ms;
}
#story-container .story-title:hover {
	background: #666666;
}

.story-view img {
	max-width: 100%;
}



